@import "../_mixins/styles-mixin";                                // mixins
@import "../_parts/settings";                                     // settings
@import "../_parts/hamburger";                                    // settings

/* styles for header --> */
.header {
  background-color: #232323;
  padding: 15px 0;
  .main-menu {
    &__content {
      display: flex;
      flex-direction: row;
      align-items: stretch;
      justify-content: flex-start;
      flex-wrap: nowrap;
      width: 100%;
      max-width: 100%;
      flex-basis: 100%; }
    .mmenu-list {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      flex-wrap: wrap;
      width: 100%;
      max-width: 100%;
      flex-basis: 100%;
      &__item {
        display: inline-block;
        margin-right: 20px;
        &:last-child {
          margin-right: 0; } }
      &__link {
        color: #979797;
        transition: color .3s;
        &:hover {
          color: #fff; }
        &.active {
          color: #fff; } } } } }
