@import "_mixins/styles-mixin";                              // mixins
@import "_parts/settings";                                   // settings
@import "_parts/reset";
@import "_fonts/fonts";
// @import "_plugins/parallax"                              // parallax (need include first)
@import "_parts/_header";

/* styles for first viewport --> */
.main {
  margin: 5vh 0;
  &__content {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    justify-content: flex-start;
    flex-wrap: wrap;
    width: 100%;
    max-width: 100%;
    flex-basis: 100%;
    background-color: #232323;
    height: calc(90vh - 50px);
    &__left-side {
      flex-basis: 25%;
      max-width: 25%;
      width: 100%;
      padding: 40px 20px;
      .image-block {
        img {
          display: block;
          width: auto;
          max-width: 180px;
          border-radius: 50%;
          margin: 0 auto;
          box-shadow: 0px 0px 15px -5px #000; } }
      .descr-block {
        padding: 20px 0;
        text-align: center;
        h2 {
          font-size: 20px;
          line-height: 20px;
          margin: 10px 0; }
        .social-links {
          display: flex;
          flex-direction: row;
          align-items: baseline;
          justify-content: center;
          flex-wrap: wrap;
          width: 100%;
          max-width: 100%;
          flex-basis: 100%;
          margin: 15px 0;
          &__item {
            display: inline-block;
            margin: 0 5px; }
          &__link {
            color: $main_color;
            transition: color .3s;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;
            border-radius: 50%;
            overflow: hidden;
            box-shadow: 0px 0px 0px 1px #aaa inset;
            svg {
              display: inline-block;
              height: auto;
              width: 30px;
              & * {
                fill: $main_color;
                transition: fill .3s; } }
            &:hover {
              color: #fff;
              svg {
                & * {
                  fill: #fff; } } } } }
        .mail-link {
          display: inline-block;
          color: $main_color;
          transition: color .3s;
          font-size: 18px;
          line-height: 20px;
          margin-bottom: 10px;
          letter-spacing: .5px;
          &:hover {
            color: #fff; } }
        .resume-link {
          font-size: 18px;
          line-height: 20px;
          color: $main_color;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          flex-wrap: nowrap;
          width: auto;
          flex-basis: auto;
          transition: color .3s;
          text-align: center;
          margin: 0 auto;
          span {
            margin-left: 10px; }
          svg {
            display: inline-block;
            width: 30px;
            height: auto;
            & * {
              fill: $main_color;
              transition: fill .3s; } }
          &:hover {
            color: #fff;
            svg {
              & * {
                fill: #fff; } } } } } }
    &__right-side {
      flex-basis: 75%;
      max-width: 75%;
      width: 100%;
      padding: 40px 30px 40px 40px;
      position: relative;
      max-height: 100%;
      &:before {
        content: '';
        display: block;
        width: 1px;
        height: 95%;
        position: absolute;
        left: 0;
        top: 2.5%;
        background-color: rgba(#aaa, .05); }
      .main-tab {
        display: none;
        max-height: 100%;
        overflow-y: scroll;
        position: absolute;
        top: 40px;
        left: 20px;
        width: 100%;
        height: 100%;
        max-width: calc(100% - 50px);
        max-height: calc(100% - 80px);
        padding: 0 40px 20px 20px;
        &::-webkit-scrollbar-track {
          -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
          background-color: #232323; }
        &::-webkit-scrollbar {
          width: 5px;
          background-color: $main_color; }
        &::-webkit-scrollbar-thumb {
          background-color: $main_color; }
        .info-block {
          h2 {
            margin-bottom: 20px;
            font-size: 25px;
            line-height: 30px;
            span {
              color: #fff;
              font-family: RobotoMedium, sans-serif; } }
          p {
            margin-bottom: 15px;
            font-size: 18px;
            line-height: 24px;
            &:last-child {
              margin-bottom: 0; } } }
        .get-in-touch {
          margin-top: 20px;
          padding-top: 20px;
          border-top: 1px solid rgba(170,170,170,0.05);
          h2 {
            color: #fff;
            display: block;
            margin-bottom: 15px;
            font-family: RobotoMedium, sans-serif; }
          p {
            margin-bottom: 10px;
            font-size: 18px;
            line-height: 24px;
            &:last-child {
              margin-bottom: 0; } } }
        .skills-block {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          flex-wrap: wrap;
          width: 100%;
          max-width: 100%;
          flex-basis: 100%;
          &__item {
            flex-basis: 47%;
            max-width: 47%;
            width: 100%;
            margin-bottom: 2rem;
            .title-row {
              display: flex;
              flex-direction: row;
              align-items: baseline;
              justify-content: space-between;
              flex-wrap: wrap;
              width: 100%;
              max-width: 100%;
              flex-basis: 100%;
              font-size: 14px;
              line-height: 20px;
              .exp {
                color: #fff;
                font-size: 16px;
                line-height: 20px; } }
            .range-row {
              display: block;
              width: 100%;
              height: 10px;
              position: relative;
              background: #191919;
              border-radius: 3px;
              margin-top: 10px;
              overflow: hidden;
              span {
                width: 0%;
                display: block;
                height: 100%;
                background-color: #aaa;
                box-shadow: inset 0 2px 9px  rgba(255,255,255,0.3), inset 0 -2px 6px rgba(0,0,0,0.4);
                position: relative;
                overflow: hidden;
                transition: width 1s; }
              &.loaded {
                &[data-exp="Beginner"] {
                  span {
                    width: 25%; } }
                &[data-exp="Basic"] {
                  span {
                    width: 50%; } }
                &[data-exp="Intermediate"] {
                  span {
                    width: 75%; } }
                &[data-exp="Advanced"] {
                  span {
                    width: 100%; } } } } }

          &__title {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-start;
            flex-wrap: nowrap;
            flex-basis: 100%;
            max-width: 100%;
            margin-top: 20px;
            padding-bottom: 10px;
            border-bottom: 1px solid rgba(170,170,170,0.05);
            margin-bottom: 15px;
            svg {
              fill: $text;
              height: 28px;
              width: auto;
              display: inline-block;
              margin-right: 12px; }
            &:nth-of-type(1) {
              margin-top: 0; } } }
        .experience-block {
          .experience-list {
            &__item {
              display: block;
              width: 100%;
              margin-bottom: 30px;
              padding: 20px;
              background-color: #282828;
              box-shadow: 0px 12px 17px rgba(0, 0, 0, 0.1), 0px 5px 22px rgba(0, 0, 0, 0.1), 0px 7px 8px rgba(0, 0, 0, 0.1);
              &:last-child {
                margin-bottom: 0; }
              .time {
                color: $main_color; }
              .title {
                color: #fff;
                font-size: 24px;
                line-height: 24px;
                margin: 5px 0;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: flex-start;
                flex-wrap: nowrap;
                width: auto;
                flex-basis: auto;
                max-width: 100%;
                margin: 10px 0;
                a {
                  color: #aaa;
                  display: inline-flex;
                  width: 22px;
                  height: auto;
                  transition: color .3s;
                  margin-left: 10px;
                  svg {
                    width: 22px;
                    height: auto; } } }

              .descr {
                padding-left: 20px;
                font-size: 16px;
                line-height: 24px;
                position: relative;
                margin: 10px 0;
                &:before {
                  content: '';
                  display: block;
                  width: 1px;
                  height: 100%;
                  position: absolute;
                  left: 2px;
                  top: 0px;
                  background-color: rgba(170,170,170,0.2); } }
              .info {
                margin-top: 20px;
                .type {
                  display: inline-block;
                  padding: 0 8px;
                  border: 1px solid $main_color;
                  margin-right: 10px;
                  font-size: 14px;
                  line-height: 25px;
                  color: $main_color;
                  &:last-child {
                    margin-right: 0; } } } } } }
        .projects-filter {
          position: relative;
          max-width: 750px;
          width: 100%;
          height: 30px;
          margin-bottom: 20px;
          top: -20px;
          .filter-list {
            position: fixed;
            padding: 15px 18px 15px 15px;
            background-color: #232323;
            margin: 0 -15px;
            z-index: 1;
            width: 100%;
            max-width: inherit;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;
            .tag-title {
              color: #979797;
              display: inline-block;
              margin-right: 10px; }
            &__type, &__lang {
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: flex-start;
              flex-wrap: wrap;
              width: auto;
              flex-basis: auto;
              .input-group {
                flex-basis: auto;
                width: auto;
                &:last-child {
                  margin-right: 0; }
                input {
                  display: none; }
                label {
                  display: inline-block;
                  padding: 0 8px;
                  border: 1px solid $main_color;
                  margin-right: 10px;
                  font-size: 14px;
                  line-height: 25px;
                  color: $main_color;
                  cursor: pointer;
                  transition: border-color .3s, color .3s;
                  span {
                    transition: color .3s; }
                  &.active {
                    border-color: #fff; }
                  input:checked + span {
                    color: #fff; } } } } } }
        .projects-block {
          &__item {
            display: block;
            width: 100%;
            margin-bottom: 30px;
            padding: 20px;
            background-color: #282828;
            box-shadow: 0px 12px 17px rgba(0, 0, 0, 0.1), 0px 5px 22px rgba(0, 0, 0, 0.1), 0px 7px 8px rgba(0, 0, 0, 0.1);
            position: relative;
            &:nth-of-type(1) {
              background-color: #303030; }
            &:nth-of-type(2) {
              background-color: #2b2b2b; }
            &:last-child {
              margin-bottom: 0; }
            .name {
              display: block;
              width: 100%;
              color: #fff;
              line-height: 28px; }
            .descr {
              margin: 15px 0;
              padding-left: 20px;
              position: relative;
              &:before {
                content: '';
                display: block;
                width: 1px;
                height: 100%;
                position: absolute;
                left: 2px;
                top: 0px;
                background-color: rgba(170,170,170,0.2); }
              p {
                margin-bottom: 10px;
                &:last-child {
                  margin-bottom: 0; } } }
            .info {
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: space-between;
              flex-wrap: nowrap;
              width: 100%;
              max-width: 100%;
              flex-basis: 100%;
              margin-top: 20px; }
            .tags-list {
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: flex-start;
              flex-wrap: wrap;
              width: 100%;
              max-width: 100%;
              flex-basis: 100%;
              &__item {
                display: inline-block;
                padding: 0 8px;
                border: 1px solid $main_color;
                margin-right: 10px;
                font-size: 14px;
                line-height: 25px;
                color: $main_color;
                &:last-child {
                  margin-right: 0; } } }
            .project-link, .project-src {
              color: $main_color;
              display: inline-block;
              width: 22px;
              height: 22px;
              transition: color .3s;

              &:hover {
                color: #fff; } }
            .project-src {
              margin-right: 12px;
              svg {
                width: auto;
                height: 22px; } }

            &:after {
              content: attr(data-type);
              position: absolute;
              top: 11px;
              right: 55px;
              padding: 2px 0;
              display: inline-block;
              font-size: 12px;
              line-height: 12px;
              color: #aaa;
              transition: border-color .3s, color .3s;
              text-transform: capitalize; }
            &:before {
              content: attr(data-lang);
              position: absolute;
              top: 11px;
              right: 11px;
              display: inline-block;
              border-left: 1px solid #aaa;
              padding: 2px 0 2px 8px;
              margin-right: 10px;
              font-size: 12px;
              line-height: 12px;
              color: #aaa;
              transition: border-color .3s, color .3s;
              text-transform: capitalize; } } } } } } }





